
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import querystring from 'vux/src/tools/querystring/index.js'


import FullImage from '@/components/IFullImage/index'
import '../assets/css/arrow.css';
import '../assets/css/wechat.css';

export default {
  components: {
    Swiper,
    SwiperItem,
    XInput,
    XButton,
    FullImage
  },
  data() {
    return {
      showArrow: true,
      now: '00:00',
      swiperItemIndex: 0,
      myChat:''
    }
  },
  computed: {
    today() {
      return dateFormat(new Date(), 'M月D日 星期E');
    },
    screenHeight() {
      return window.innerHeight + 'px';
    },
    chatHeight() {
      return (window.innerHeight - 50) + 'px';
    }
  },
  methods: {
    refreshTime() {
      setInterval(() => {
        this.now = dateFormat(new Date(), 'HH:mm');
      }, 1000);
    },
    onSwiperItemIndexChange(index) {
      this.showArrow = index == 0;
      if(index==0){
        document.title='';
      }else{
        document.title='品质成钞两周年';
      }
    },
  },
  mounted() {
    this.refreshTime();

    let param = querystring.parse('a=b&c=d');
    console.log(param);
  }
}




